.dropzone {
  position: relative;
  min-height: 34px; }

.dropzone-overlay {
  position: absolute;
  top: 0;
  left: 0;
  width: 100%;
  height: 100%;
  opacity: 0;
  z-index: -1;
  text-align: center;
  border-radius: 0.25rem;
  background: rgba(250, 226, 72, 0.65);
  -webkit-transition: opacity 0.1s, background 0.1s, z-index 0s 0.1s;
  transition: opacity 0.1s, background 0.1s, z-index 0s 0.1s; }
  .ondrag .dropzone-overlay {
    opacity: 1;
    z-index: 1;
    -webkit-transition: opacity 0.1s, background 0.1s, z-index 0s 0s;
    transition: opacity 0.1s, background 0.1s, z-index 0s 0s; }

.dropzone-highlighted {
  background-color: rgba(0, 123, 255, 0.5); }

.dropzone-hint {
  position: absolute;
  top: 50%;
  left: 50%;
  -webkit-transform: translate(-50%, -50%);
          transform: translate(-50%, -50%); }

.file-uploader + .related-links {
  display: none; }

.file-uploader .form-control {
  overflow: hidden;
  white-space: nowrap;
  text-overflow: ellipsis;
  -webkit-transition: background-color 0.2s 0s;
  transition: background-color 0.2s 0s; }

.file-uploader .file-info {
  font-size: .875em;
  font-weight: 300; }

.file-uploader .progress {
  position: absolute;
  display: -webkit-box;
  display: -ms-flexbox;
  display: flex;
  left: 0;
  bottom: 0;
  width: 100%;
  height: 100%;
  border-radius: 0;
  opacity: 0;
  -webkit-transition: opacity 0.2s 0s;
  transition: opacity 0.2s 0s; }

.file-uploader.empty .form-control {
  border-top-right-radius: 4px;
  border-top-right-radius: 0.25rem;
  border-bottom-right-radius: 4px;
  border-bottom-right-radius: 0.25rem; }

.file-uploader.loading .form-control {
  background-color: #f5f5f5;
  border-top-right-radius: 0;
  border-bottom-right-radius: 0;
  -webkit-transition: background-color 0s;
  transition: background-color 0s; }

.file-uploader.loading .file-name,
.file-uploader.loading .file-info {
  display: none; }

.file-uploader.loading .progress {
  opacity: 0.5;
  -webkit-transition: opacity 0s;
  transition: opacity 0s; }

.file-uploader.loading .file-uploader__upload-button input {
  display: none; }

.file-uploader.processing .form-control {
  border-top-right-radius: 4px;
  border-top-right-radius: 0.25rem;
  border-bottom-right-radius: 4px;
  border-bottom-right-radius: 0.25rem; }

.file-uploader__ready-buttons,
.file-uploader__loading-buttons {
  display: -webkit-box;
  display: -ms-flexbox;
  display: flex; }
  .file-uploader__ready-buttons .input-group-text,
  .file-uploader__loading-buttons .input-group-text {
    border-top-left-radius: 0;
    border-bottom-left-radius: 0; }
    .file-uploader__ready-buttons .input-group-text:not(:last-child),
    .file-uploader__loading-buttons .input-group-text:not(:last-child) {
      border-top-right-radius: 0;
      border-bottom-right-radius: 0; }

.file-uploader.empty:not(.loading) .input-group-append,
.file-uploader.empty.processing .input-group-append {
  display: none; }

.file-uploader:not(.loading) .file-uploader__loading-buttons,
.file-uploader.processing .file-uploader__loading-buttons {
  display: none; }

.file-uploader.loading .file-uploader__ready-buttons {
  display: none; }
